.App {
  display: flex;
  justify-content: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 25px;
  flex-direction: column;
}
body {
  margin: 0%;
  padding: 0%;
}
.TitleSection {
  background-color: #50b9ff;
  width: 100%;
  height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
}
.TitleSection input {
  width: 200px;
  height: 40px;
  border-radius: 10px;
  border: 1px solid #4ca8ee;
  font-size: 18px;
  padding-left: 10px;
}
.TitleSection button {
  width: 180px;
  height: 60px;
  margin: 20px;
  border-radius: 10px;
  border: none;
  font-size: 18px;
  padding-left: 10px;
  color: white;
  background-color: #006cbf;
}
.TitleSection button:hover {
  cursor: pointer;
}

.DisplaySection {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.DisplaySection h3,
h4 {
  margin: 5px;
  color: black;
}
.DisplaySection img {
  width: 200px;
}
